@import "./styles/index.scss";

iframe {
  display: none;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.main {
  min-height: calc(100vh - 96px - 66px);
}

.container {
  max-width: 1140px;
  height: max-content;
  padding: 24px 152px 0;
  margin: 0 auto;

  // @include onTablet {
  //   padding: 30px 34px 0;
  // }

  // @include onDesktop {
  //   padding: 20px 135px 0;
  // }
}
